<template>
  <footer class="footer">
    <textarea
      rows="1"
      placeholder="Написать сообщение..."
      class="footer__edit"
      v-model="message"
    ></textarea>
    <button
      class="footer__btn"
      v-if="message.length == 0"
      @click="window = true"
    >
      <img src="@src/assets/images/send.svg" alt="" />
    </button>
    <button class="footer__btn" @click="sendMess" v-else>
      <img src="@src/assets/images/send2.svg" alt="" />
    </button>
    <div class="window" v-if="window" @click="window = false">
      <div class="window__body" @click.stop.prevent>
        <div class="window__title">Отправить картинку</div>
        <div class="window__from">
          <label>
            <span>URL</span>
            <input v-model="photo" />
          </label>
          <label>
            <span>Комментарий</span>
            <textarea v-model="comment"></textarea>
          </label>
        </div>
        <div class="window__footer">
          <button class="btn red" @click="window = false">Отмена</button>
          <button class="btn purpure" @click="sendMessPhoto">Отправить</button>
        </div>
      </div>
    </div>
  </footer>
</template>

<script>
export default {
  name: "WindowFooter",
  props: {
    sendId: Number,
  },
  data() {
    return {
      message: "",
      photo: "",
      comment: "",
      window: false,
    };
  },
  methods: {
    sendMess() {
      const hours =
        new Date().getHours() < 10
          ? "0" + new Date().getHours()
          : new Date().getHours();
      const mins =
        new Date().getMinutes() < 10
          ? "0" + new Date().getMinutes()
          : new Date().getMinutes();
      const time = `${hours}:${mins}`;
      if (this.message.length > 0) {
        const mess = {};
        mess.body = this.message.trim();
        mess.sendId = this.sendId;
        mess.time = time;
        this.$emit("new-mess", mess);
        this.message = "";
      }
      console.log(time);
    },
    sendMessPhoto() {
      const hours =
        new Date().getHours() < 10
          ? "0" + new Date().getHours()
          : new Date().getHours();
      const mins =
        new Date().getMinutes() < 10
          ? "0" + new Date().getMinutes()
          : new Date().getMinutes();
      const time = `${hours}:${mins}`;
      if (this.photo.length > 0) {
        const mess = {};
        mess.body = this.comment.trim();
        mess.photo = this.photo.trim();
        mess.sendId = this.sendId;
        mess.time = time;
        this.$emit("new-mess", mess);
        this.comment = this.photo = "";
        this.window = false;
      }
    },
  },
};
</script>

